y: inline; }\n  .fr-l { float: right; _display: inline; }\n  .fn-l { float: none; }\n}\n","\n// Converted Variables\n\n\n// Custom Media Query Variables\n\n\n/*\n\n   FONT FAMILY GROUPS\n   Docs: http://tachyons.io/docs/typography/font-family/\n\n*/\n\n\n.sans-serif {\n  font-family: $sans-serif;\n}\n\n.serif {\n  font-family: $serif;\n}\n\n.system-sans-serif {\n  font-family: sans-serif;\n}\n\n.system-serif {\n  font-family: serif;\n}\n\n\n/* Monospaced Typefaces (for code) */\n\n/* From http://cssfontstack.com */\ncode, .code {\n  font-family: Consolas,\n               monaco,\n               monospace;\n}\n\n.courier {\n  font-family: 'Courier Next',\n               courier,\n               monospace;\n}\n\n\n/* Sans-Serif Typefaces */\n\n.helvetica {\n  font-family: 'helvetica neue', helvetica,\n               sans-serif;\n}\n\n.avenir {\n  font-family: 'avenir next', avenir,\n               sans-serif;\n}\n\n\n/* Serif Typefaces */\n\n.athelas {\n  font-family: athelas,\n               georgia,\n               serif;\n}\n\n.georgia {\n  font-family: georgia,\n               serif;\n}\n\n.times {\n  font-family: times,\n               serif;\n}\n\n.bodoni {\n  font-family: \"Bodoni MT\",\n                serif;\n}\n\n.calisto {\n  font-family: \"Calisto MT\",\n                serif;\n}\n\n.garamond {\n  font-family: garamond,\n               serif;\n}\n\n.baskerville {\n  font-family: baskerville,\n               serif;\n}\n\n","\n// Converted Variables\n\n\n// Custom Media Query Variables\n\n\n/*\n\n   FONT STYLE\n   Docs: http://tachyons.io/docs/typography/font-style/\n\n   Media Query Extensions:\n     -ns = not-small\n     -m  = medium\n     -l  = large\n\n*/\n\n.i         { font-style: italic; }\n.fs-normal { font-style: normal; }\n\n@media #{$breakpoint-not-small} {\n  .i-ns       { font-style: italic; }\n  .fs-normal-ns     { font-style: normal; }\n}\n\n@media #{$breakpoint-medium} {\n  .i-m       { font-style: italic; }\n  .fs-normal-m     { font-style: normal; }\n}\n\n@media #{$breakpoint-large} {\n  .i-l       { font-style: italic; }\n  .fs-normal-l     { font-style: normal; }\n}\n","\n// Converted Variables\n\n\n// Custom Media Query Variables\n\n\n/*\n\n   FONT WEIGHT\n   Docs: http://tachyons.io/docs/typography/font-weight/\n\n   Base\n     fw = font-weight\n\n   Modifiers:\n     1 = literal value 100\n     2 = literal value 200\n     3 = literal value 300\n     4 = literal value 400\n     5 = literal value 500\n     6 = literal value 600\n     7 = literal value 700\n     8 = literal value 800\n     9 = literal value 900\n\n   Media Query Extensions:\n     -ns = not-small\n     -m  = medium\n     -l  = large\n\n*/\n\n.normal { font-weight: normal; }\n.b      { font-weight: bold; }\n.fw1    { font-weight: 100; }\n.fw2    { font-weight: 200; }\n.fw3    { font-weight: 300; }\n.fw4    { font-weight: 400; }\n.fw5    { font-weight: 500; }\n.fw6    { font-weight: 600; }\n.fw7    { font-weight: 700; }\n.fw8    { font-weight: 800; }\n.fw9    { font-weight: 900; }\n\n\n@media #{$breakpoint-not-small} {\n  .normal-ns { font-weight: normal; }\n  .b-ns      { font-weight: bold; }\n  .fw1-ns    { font-weight: 100; }\n  .fw2-ns    { font-weight: 200; }\n  .fw3-ns    { font-weight: 300; }\n  .fw4-ns    { font-weight: 400; }\n  .fw5-ns    { font-weight: 500; }\n  .fw6-ns    { font-weight: 600; }\n  .fw7-ns    { font-weight: 700; }\n  .fw8-ns    { font-weight: 800; }\n  .fw9-ns    { font-weight: 900; }\n}\n\n@media #{$breakpoint-medium} {\n  .normal-m { font-weight: normal; }\n  .b-m      { font-weight: bold; }\n  .fw1-m    { font-weight: 100; }\n  .fw2-m    { font-weight: 200; }\n  .fw3-m    { font-weight: 300; }\n  .fw4-m    { font-weight: 400; }\n  .fw5-m    { font-weight: 500; }\n  .fw6-m    { font-weight: 600; }\n  .fw7-m    { font-weight: 700; }\n  .fw8-m    { font-weight: 800; }\n  .fw9-m    { font-weight: 900; }\n}\n\n@media #{$breakpoint-large} {\n  .normal-l { font-weight: normal; }\n  .b-l      { font-weight: bold; }\n  .fw1-l    { font-weight: 100; }\n  .fw2-l    { font-weight: 200; }\n  .fw3-l    { font-weight: 300; }\n  .fw4-l    { font-weight: 400; }\n  .fw5-l    { font-weight: 500; }\n  .fw6-l    { font-weight: 600; }\n  .fw7-l    { font-weight: 700; }\n  .fw8-l    { font-weight: 800; }\n  .fw9-l    { font-weight: 900; }\n}\n","\n// Converted Variables\n\n\n// Custom Media Query Variables\n\n\n/*\n\n   FORMS\n   \n*/\n\n.input-reset {\n  -webkit-appearance: none;\n  -moz-appearance: none;\n}\n\n.button-reset::-moz-focus-inner,\n.input-reset::-moz-focus-inner {\n  border: 0;\n  padding: 0;\n}\n","\n// Converted Variables\n\n\n// Custom Media Query Variables\n\n\n/*\n\n   HEIGHTS\n   Docs: http://tachyons.io/docs/layout/heights/\n\n   Base:\n     h = height\n     min-h = min-height\n     min-vh = min-height vertical screen height\n     vh = vertical screen height\n\n   Modifiers\n     1 = 1st step in height scale\n     2 = 2nd step in height scale\n     3 = 3rd step in height scale\n     4 = 4th step in height scale\n     5 = 5th step in height scale\n\n     -25   = literal value 25%\n     -50   = literal value 50%\n     -75   = literal value 75%\n     -100  = literal value 100%\n\n     -auto = string value of auto\n     -inherit = string value of inherit\n\n   Media Query Extensions:\n     -ns = not-small\n     -m  = medium\n     -l  = large\n\n*/\n\n/* Height Scale */\n\n.h1 { height: $height-1; }\n.h2 { height: $height-2; }\n.h3 { height: $height-3; }\n.h4 { height: $height-4; }\n.h5 { height: $height-5; }\n\n/* Height Percentages - Based off of height of parent */\n\n.h-25 {  height:  25%; }\n.h-50 {  height:  50%; }\n.h-75 {  height:  75%; }\n.h-100 { height: 100%; }\n\n.min-h-100 { min-height: 100%; }\n\n/* Screen Height Percentage */\n\n.vh-25 {  height:  25vh; }\n.vh-50 {  height:  50vh; }\n.vh-75 {  height:  75vh; }\n.vh-100 { height: 100vh; }\n\n.min-vh-100 { min-height: 100vh; }\n\n\n/* String Properties */\n\n.h-auto {     height: auto; }\n.h-inherit {  height: inherit; }\n\n@media #{$breakpoint-not-small} {\n  .h1-ns {  height: $height-1; }\n  .h2-ns {  height: $height-2; }\n  .h3-ns {  height: $height-3; }\n  .h4-ns {  height: $height-4; }\n  .h5-ns {  height: $height-5; }\n  .h-25-ns { height: 25%; }\n  .h-50-ns { height: 50%; }\n  .h-75-ns { height: 75%; }\n  .h-100-ns { height: 100%; }\n  .min-h-100-ns { min-height: 100%; }\n  .vh-25-ns {  height:  25vh; }\n  .vh-50-ns {  height:  50vh; }\n  .vh-75-ns {  height:  75vh; }\n  .vh-100-ns { height: 100vh; }\n  .min-vh-100-ns { min-height: 100vh; }\n  .h-auto-ns { height: auto; }\n  .h-inherit-ns { height: inherit; }\n}\n\n@media #{$breakpoint-medium} {\n  .h1-m { height: $height-1; }\n  .h2-m { height: $height-2; }\n  .h3-m { height: $height-3; }\n  .h4-m { height: $height-4; }\n  .h5-m { height: $height-5; }\n  .h-25-m { height: 25%; }\n  .h-50-m { height: 50%; }\n  .h-75-m { height: 75%; }\n  .h-100-m { height: 100%; }\n  .min-h-100-m { min-height: 100%; }\n  .vh-25-m {  height:  25vh; }\n  .vh-50-m {  height:  50vh; }\n  .vh-75-m {  height:  75vh; }\n  .vh-100-m { height: 100vh; }\n  .min-vh-100-m { min-height: 100vh; }\n  .h-auto-m { height: auto; }\n  .h-inherit-m { height: inherit; }\n}\n\n@media #{$breakpoint-large} {\n  .h1-l { height: $height-1; }\n  .h2-l { height: $height-2; }\n  .h3-l { height: $height-3; }\n  .h4-l { height: $height-4; }\n  .h5-l { height: $height-5; }\n  .h-25-l { height: 25%; }\n  .h-50-l { height: 50%; }\n  .h-75-l { height: 75%; }\n  .h-100-l { height: 100%; }\n  .min-h-100-l { min-height: 100%; }\n  .vh-25-l {  height:  25vh; }\n  .vh-50-l {  height:  50vh; }\n  .vh-75-l {  height:  75vh; }\n  .vh-100-l { height: 100vh; }\n  .min-vh-100-l { min-height: 100vh; }\n  .h-auto-l { height: auto; }\n  .h-inherit-l { height: inherit; }\n}\n","\n// Converted Variables\n\n\n// Custom Media Query Variables\n\n\n/*\n\n   LETTER SPACING\n   Docs: http://tachyons.io/docs/typography/tracking/\n\n   Media Query Extensions:\n     -ns = not-small\n     -m  = medium\n     -l  = large\n\n*/\n\n.tracked       { letter-spacing:  $letter-spacing-1; }\n.tracked-tight { letter-spacing: $letter-spacing-tight; }\n.tracked-mega  { letter-spacing:  $letter-spacing-2; }\n\n@media #{$breakpoint-not-small} {\n  .tracked-ns       { letter-spacing:  $letter-spacing-1; }\n  .tracked-tight-ns { letter-spacing: $letter-spacing-tight; }\n  .tracked-mega-ns  { letter-spacing:  $letter-spacing-2; }\n}\n\n@media #{$breakpoint-medium} {\n  .tracked-m       { letter-spacing:  $letter-spacing-1; }\n  .tracked-tight-m { letter-spacing: $letter-spacing-tight; }\n  .tracked-mega-m  { letter-spacing:  $letter-spacing-2; }\n}\n\n@media #{$breakpoint-large} {\n  .tracked-l       { letter-spacing:  $letter-spacing-1; }\n  .tracked-tight-l { letter-spacing: $letter-spacing-tight; }\n  .tracked-mega-l  { letter-spacing:  $letter-spacing-2; }\n}\n","\n// Converted Variables\n\n\n// Custom Media Query Variables\n\n\n/*\n\n   LINE HEIGHT / LEADING\n   Docs: http://tachyons.io/docs/typography/line-height\n\n   Media Query Extensions:\n     -ns = not-small\n     -m  = medium\n     -l  = large\n\n*/\n\n  .lh-solid { line-height: $line-height-solid; }\n  .lh-title { line-height: $line-height-title; }\n  .lh-copy  { line-height: $line-height-copy; }\n\n@media #{$breakpoint-not-small} {\n  .lh-solid-ns { line-height: $line-height-solid; }\n  .lh-title-ns { line-height: $line-height-title; }\n  .lh-copy-ns  { line-height: $line-height-copy; }\n}\n\n@media #{$breakpoint-medium} {\n  .lh-solid-m { line-height: $line-height-solid; }\n  .lh-title-m { line-height: $line-height-title; }\n  .lh-copy-m  { line-height: $line-height-copy; }\n}\n\n@media #{$breakpoint-large} {\n  .lh-solid-l { line-height: $line-height-solid; }\n  .lh-title-l { line-height: $line-height-title; }\n  .lh-copy-l  { line-height: $line-height-copy; }\n}\n\n","\n// Converted Variables\n\n\n// Custom Media Query Variables\n\n\n/*\n\n   LINKS\n   Docs: http://tachyons.io/docs/elements/links/\n\n*